nav .collapsible label {
  border-bottom: 0;
  padding: 0
}
nav .collapsible > button {
  background-color: #fff;
  display: none;
  font-size: .5rem;
  margin-right: 1rem;
  padding: .25rem;
  position: absolute;
  right: 0;
  top: .2rem
}
@media only screen and (max-width: 768px) {
nav .collapsible > button {
    display: block
}
}



















































